import React, { useState, useRef, useEffect } from 'react';


export default function Search({props}) {
	const {setSearchTerm, searchTerm, searchRef, handleSubmit} = props

	const [hasScrolled, setHasScrolled] = useState(false)
	const ref = useRef(null)

	const scroll = () => {
		if(window.innerWidth > 500){
			if(window.scrollY >= 225){
				setHasScrolled(true)
			} else {
				setHasScrolled(false)
			}
		} else {
			if(window.scrollY >= 184){
				setHasScrolled(true)
			} else {
				setHasScrolled(false)
			}
		}
	
	}

	useEffect(() => {
		window.addEventListener('scroll', scroll)
		return () => { window.removeEventListener('scroll', scroll)}
	}, [])


	return (
		<>
		<div className={hasScrolled ? 'spacer' : null}></div>

		<form ref={ref} 
			  className={hasScrolled ? "search-container active" : "search-container"}
			  onSubmit={(e) => handleSubmit(e)}
			>
			<button type="submit">
			<svg className="search-glass" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0z"/><path d="M18.031 16.617l4.283 4.282-1.415 1.415-4.282-4.283A8.96 8.96 0 0 1 11 20c-4.968 0-9-4.032-9-9s4.032-9 9-9 9 4.032 9 9a8.96 8.96 0 0 1-1.969 5.617zm-2.006-.742A6.977 6.977 0 0 0 18 11c0-3.868-3.133-7-7-7-3.868 0-7 3.132-7 7 0 3.867 3.132 7 7 7a6.977 6.977 0 0 0 4.875-1.975l.15-.15z"/></svg></button>
			
			<input type="text"
					value={searchTerm}
					onChange={(e) => {setSearchTerm(e.target.value)}}
					ref={searchRef} />
		</form>
		</>
	);
}
